
#main-map{position:absolute;top:0;left:0;right:0;bottom:0;width:100%;height:100%
}
.mapItem .title{font-size:1.2em;font-weight:normal;margin:0
}
.mapItem .date{color:#999;font-size:12px
}
.myPosition{width:60px;height:75px;overflow:hidden
}
.myPosition img{width:100%
}
.controls #menu{position:absolute;top:10px;left:10px;z-index:2;border:0;cursor:pointer;border-radius:2px;color:#000;font-family:Roboto, Arial, sans-serif;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;font-size:16px;background-color:#fff;padding:8px;background-clip:padding-box;box-shadow:rgba(0,0,0,0.298039) 0px 1px 4px -1px;font-weight:500
}
.controls #myLocation{position:absolute;top:10px;right:10px;z-index:2;border-radius:2px;border:0;cursor:pointer;color:#000;font-family:Roboto, Arial, sans-serif;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;font-size:11px;background-color:#fff;padding:11px;background-clip:padding-box;box-shadow:rgba(0,0,0,0.298039) 0px 1px 4px -1px;font-weight:500
}



