
ons-splitter-mask{background-color:rgba(0,0,0,0.5)
}
#sidemenu{overflow:hidden;overflow-y:auto;-webkit-overflow-scrolling:touch;background-color:#4D5250;background-color:#4D5050;background-color:#303e4d;color:#eaeaea
}
#sidemenu hr{opacity:0;margin:12px 0 24px 0
}
#sidemenu .phi-menu-label{padding:0 24px
}
#sidemenu header h1{color:#fff;text-shadow:0 2px 2px rgba(0,0,0,0.6);-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center
}
#sidemenu header i.phi-media-right{-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center;color:#fff;opacity:0.4
}
#sidemenu header .school h1{padding:24px;padding-bottom:12px;font-size:1.3em;white-space:nowrap;overflow:hidden;text-overflow:ellipsis
}
#sidemenu header .user{cursor:pointer
}
#sidemenu header .user h1{opacity:0.8;font-size:1em
}
#sidemenu header .user .phi-media{padding:8px 24px
}
#sidemenu header .user .phi-avatar{width:28px;height:28px;min-width:0;min-height:0;margin-right:12px
}
#sidemenu header .user .phi-drawer{padding:12px 0
}
#sidemenu .main-options{margin-top:24px
}
.slide-enter-active,.slide-leave-active{position:absolute;top:0;left:0;right:0;width:100%;transition:opacity .42s
}
.slide-enter-active .phi-page-contents,.slide-enter-active .phi-page-navigation>*,.slide-leave-active .phi-page-contents,.slide-leave-active .phi-page-navigation>*{transition:transform .42s
}
.slide-leave-active{opacity:0
}
.slide-enter,.slide-enter-active{z-index:1
}
.slide-leave,.slide-leave-active{z-index:2
}
.phi-page-cover{transition:height .42s,padding .42s
}
.slide-enter-active .phi-page-toolbar,.slide-leave-active .phi-page-toolbar{transition:transform .42s
}
.slide-enter .phi-page-toolbar{transform:translate3d(0, -100%, 0)
}
.slide-leave-active .phi-page-toolbar{transform:translate3d(0, -100%, 0)
}
.move-left .slide-enter .phi-page-contents,.move-left .slide-enter .phi-page-navigation>*{transform:translate3d(210px, 0, 0)
}
.move-left .slide-leave-active .phi-page-contents,.move-left .slide-leave-active .phi-page-navigation>*{transform:translate3d(-210px, 0, 0)
}
.move-right .slide-enter .phi-page-contents,.move-right .slide-enter .phi-page-navigation>*{transform:translate3d(-210px, 0, 0)
}
.move-right .slide-leave-active .phi-page-contents,.move-right .slide-leave-active .phi-page-navigation>*{transform:translate3d(210px, 0, 0)
}
#main-page .page__content{top:0;padding-top:44px
}
#main-page .navigation-bar{opacity:1;transform:translate3d(0, 0, 0);transition:opacity 0.3s ease-in-out, transform 0.3s ease-in-out
}
#main-page.hidden-bar .navigation-bar{opacity:0;transform:translate3d(0, -44px, 0)
}
#main-page.hidden-bar .navigation-bar--material{opacity:0;transform:translate3d(0, -56px, 0)
}
#main-page .navigation-bar--material+.page__background+.page__content{padding-top:56px
}



